export const getAjaxData = (url, params) => {
  return axios.get(url, {params: params})
    .then(res => {
      return res.data
    }).catch(err => {
      return Object.assign({code: -99}, err)
    })
}

export const postAjaxData = (url, data) => {
  return axios.post(url, data)
    .then(res => {
      return res.data
    }).catch(err => {
      return Object.assign({code: -99}, err)
    })
}



