
import { mapActions, mapState } from "vuex";
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Search from 'vux/src/components/search/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import dateFormat from 'vux/src/tools/date/format.js'

import NoData from "@/components/noData";
import Scroll from "../components/scrollAll";
import Scroll1 from "../components/scroll1";
import { state, classify } from "@/common/js/classify";

export default {
/**
*    
* @dateQuery 
* @clearType 
* @selectedParams 
* @selectedUser 
* @enter 
* @statClick 
*/
name: "Query",
props: {
types: {
type: Array,
default: () => [
{
type: 4,
name: "",
/**
* 
* type
*
* */
notDict: false, //true
list: [
{
ID: 0,
name: ""
}
]
}
]
},
isDate: {
type: Boolean,
default: true
},
isClass: {
type: Boolean,
default: true
},
isOrder: {
type: Boolean,
default: false
},
isPerson: {
type: Boolean,
default: true
},
isQurey: {
type: Boolean,
default: false
},
isStatType: {
type: Boolean,
default: false
},
isStatState: {
type: Boolean,
default: false
},
isChooseDate: {
type: Boolean,
default: false
},

placeholder: {
type: String,
default: ""
},
formatDate: {
type: String,
default: "YYYY-MM-DD HH:mm"
},
dateTitle: {
type: String,
default: ""
},

defaultDate: {
type: Object
},
isClearDateBtn: {
type: Boolean,
default: true
},
isAuth: {
type: Boolean,
default: true
}
},
data() {
return {
date: {},
queryType: 0,
state: {},
queryList: [],
leftType: 0,
orderType: "",
orderStateList: [],
selected: {},
orderState: "",
okOrder: "",
keyWord: "",
userList: [],
historyUserList: [],


showGroupUserList: false,
groupUserList: [],
groupList: [],
groupMenuList: [],
userPage: 1,
userPageSize: 10,
showUser: 0,
pullUp: {
threshold: 0,
txt: {
more: "",
noMore: ""
}
},
pullDown: {
threshold: 50,
stop: 20
},
queryName: "",
UserID: 0,
statType: classify,
statTypeIndex: 0,
statState: state,
statStateIndex: 0,
showChooseDate: "",
selectDateMouth: "",
selectDateDay: "",
showYear: new Date().getFullYear(),
showMouth: new Date().getMonth() + 1,
MaxYear: new Date().getFullYear(),
chooseDateFormat: 0,
dateOnce: true,
isDataTimeShow: false
};
},
created() {
this.getGroup();
},
computed: {
...mapState(["isMyClient", "isAdmin"])
},
activated() {
this.dateinit();
},
mounted() {
this.dateinit();
this.showUser = 0;
let box = document.querySelectorAll(".query_box");
this.types.map((item, index) => {
if (item.notDict) {
this.state[item.type] = item.list;
if (index == 0) {
this.leftType = item.type;
this.queryList = [...this.state[item.type]];
}
} else {
this.initDictPromise(item.type).then(res => {
this.state[item.type] = this.dict[item.type];
this.state[item.type].forEach(data => {
data.name = data.DictName;
});
if (index == 0) {
this.leftType = item.type;
this.queryList = [...this.state[item.type]];
}
});
}
this.selected[item.type] = item.init || 0;
});
this.UserID = this.$store.state.CurrentUserID;
if (this.isOrder) {
this.orderType = "";
axios.get("/Common/GetPubDictionaryList?dicType=20").then(({ data }) => {
if (data.State === 1) {
this.orderStateList = data.Data;
}
});
}
},
methods: {
clickDataTime() {
this.queryType = 7;
this.isDataTimeShow = true;

},
...mapActions(["initDictPromise"]),

clearClass() {
this.types.map((item, index) => {
this.selected[item.type] = item.init || 0;
});
},
clearType() {
this.$emit("clearType");
this.queryType = 0;
this.clearClass(this.types);
},
clearOrderType() {
this.orderState = 0;
this.queryType = 0;
this.$emit("clearOrderType");
this.orderType = "";
},
sendDate() {
if (this.date.beginDate) {
this.date.endDate =
this.date.endDate || dateFormat(new Date(), this.formatDate);
}
this.$emit("dateQuery", this.date);
this.queryType = 0;
},
clearDate() {
this.date.beginDate = "";
this.date.endDate = "";
this.queryType = 0;
this.$emit("dateQuery", this.date);
},
query(item) {
this.queryType = 0;
this.selected[this.leftType] = item.ID;
this.$emit("selectedParams", item);
},
clickLeft(type) {
this.leftType = type;
this.queryList = [...this.state[type]];
},
emitOrder(type) {

this.okOrder = type;
this.queryType = 0;
this.$emit("orderDate", type);
},
emitOrderState(id) {
this.queryType = 0;
this.orderState = id;
this.$emit("orderState", id);
},
clickOrderLeft(type) {
this.orderType = type;
this.orderQueryList = [];
},
getUserList() {
if (storage.get("historyUserList")) {
this.historyUserList = storage.get("historyUserList")[
this.$store.state.CurrentUserID
];
}






axios
.get(
`/User/GetPagedUserList?keyWords=${this.keyWord}&pageIndex=${
this.userPage
}&pageSize=${this.userPageSize}`
)
.then(res => {
if (res.data.State === 1) {
if (this.userPage == 1) {
this.$refs.scroll.scroll.finishPullDown();
this.userList = res.data.Data.List;
} else {
this.userList.push(...res.data.Data.List);
this.$refs.scroll.scroll.finishPullUp();
}
}
this.$refs.scroll.forceUpdate();
this.$refs.scroll.refresh();
})
.catch(e => {
this.$refs.scroll.scroll.finishPullDown();
this.$refs.scroll.forceUpdate();
});
},





getGroup() {
let orgID = this.$store.state.orgID || 0;
if (this.$store.state.isAdmin) {
orgID = 0;
}
this.Axios.get(
`${api50}/api/v1/Org/GetOrgUserList?authType=2&orgId=${orgID}`
)
.then(res => {
res.data.Data.forEach(item => {
this.groupList.push(item);
});

this.groupList.forEach(item => {
if (item.type == 2) {
this.Axios.get(
`${api50}/api/v1/User/GetAuthUserInfo?userId=${item.ID}`
).then(res => {
this.$set(item, "showMdt", res.data.Data.Mdt);
});
}


});
})
.catch(err => {
console.log(err);
});
},

showUserList(item) {
if (item.type == 1) {
this.Axios.get(
`${api50}/api/v1/Org/GetOrgUserList?authType=2&orgId=${item.ID}`
).then(res => {
this.groupUserList = [];
this.groupUserList = res.data.Data;
this.groupUserList.forEach(item => {
if (item.type == 2) {
this.Axios.get(
`${api50}/api/v1/User/GetAuthUserInfo?userId=${item.ID}`
).then(res => {

this.$set(item, "showMdt", res.data.Data.Mdt);
});
}
});
this.showGroupUserList = true;
});
if (this.groupMenuList.length == 0) {
this.groupMenuList.push(item);
} else if (
item.ID != this.groupMenuList[this.groupMenuList.length - 1].ID
) {
this.groupMenuList.push(item);
}
} else {
item.UserID = item.ID;
this.chooseUser(item);
}
},

chooseGroupMenu(index, item) {
if (!index) {
this.showGroupUserList = false;
this.groupMenuList = [];
this.groupUserList = [];
} else {
this.showUserList(item);
this.groupMenuList.splice(index, 100);
}
},

chooseUser(item) {
this.showGroupUserList = false;
if (item.UserID) {
this.queryType = 0;
this.groupMenuList = [];
item.UserName = item.OrgName;
this.sendUser("singel", item, 2);
}
},



getUserData(status) {
if (status === 1) {
this.userPage = 1;
} else {
this.userPage++;
}
this.getUserList();
},
changePerson(index) {
this.showUser = index;
this.$nextTick(() => {
try {
this.$refs.scroll2.forceUpdate();
this.$refs.scroll2.refresh();
this.$refs.scroll1.forceUpdate();
this.$refs.scroll1.refresh();
this.$refs.scroll.forceUpdate();
this.$refs.scrol1.refresh();
} catch (e) {}
});
},
sendUser(status, item, type) {
if (item) {
this.ID = item.ID;
this.$emit("selectedUser", item.ID);
if (storage.get("historyUserList")) {
let a = storage.get("historyUserList")[
this.$store.state.CurrentUserID
];
if (a) {
let ok = true;
a.some(item1 => {
if (item.ID == item1.ID) {
ok = false;
return true;
}
});
if (ok) {
a.push(item);
}
let b = storage.get("historyUserList");
b[this.$store.state.CurrentUserID] = a;
storage.set("historyUserList", b);
} else {
a = [];
a.push(item);
let b = storage.get("historyUserList");
b[this.$store.state.CurrentUserID] = a;
storage.set("historyUserList", b);
}
} else {
let a = {};
a[this.$store.state.CurrentUserID] = [item];
storage.set("historyUserList", a);
}
} else {
if (status == "me") {
this.$emit("selectedUser", this.$store.state.CurrentUserID);
} else {
this.$emit("selectedUser", 0);
}
}
this.userPage = 1;
this.queryType = 0;
},
enter() {
this.queryType = 0;
this.$emit("enter", this.queryName);
},
dateinit() {
setTimeout(() => {
this.date = this.defaultDate
? this.defaultDate
: { beginDate: "", endDate: "" };
});
},
statClick(type, index) {

if (type == 1) {
this.statTypeIndex = index;
} else {
this.statStateIndex = index;
}
this.queryType = 0;
this.$emit("statClick", { type, index });
},
changeDateFormat(val) {
this.chooseDateFormat = val - 1;
},
changeDate(flag) {
let date = flag == 0 ? this.selectDateMouth : this.selectDateDay;
this.$emit("changeDate", flag, date);






this.dateOnce = false;
this.queryType = 0;
}
},
computed: {
...mapState(["dict"])
},
watch: {
queryType(val) {
if (val == "3") {
if (this.isPerson && this.showUser != "2") {







this.userList = [];
this.getUserList();
}
}
}
},
filters: {
tabFormat(type) {
return this.tab.find(item => item == type);
}
},
components: {
Flexbox,
FlexboxItem,
XHeader,
Group,
Datetime,
XButton,
Cell,
Tab,
TabItem,
Scroll,
Search,
XInput,
Loading,
NoData,
Scroll1
}
};



