
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import SelectOrg from "./SelectOrg/template";
import SessionUser from "./Session/template";
import AllUser from "./AllUser/template";
export default {
data() {
return {
userName: [],
checkList: [],
tabTxt: ["", "", ""],
ovoIndex: 0,
allUser: [],
discheck: [],
RadioItem: {},
radioname: ""
};
},
props: {

show: {
type: Boolean
},

defaultUser: {
type: Array,
default: () => {
return [];
}
},

title: {
type: String,
default: ""
},

radioBool: {
type: Boolean,
default: false
},

authType: {
type: Number,
default: 2
},

radioDef: {
type: [Number, String]
},

disabCheck: {
type: [Array]
}
},
components: {
XHeader,
Tab,
TabItem,
SelectOrg,
SessionUser,
AllUser
},
computed: {
radiodef() {
if (this.radioDef) {
let radioitem = this.allUser.find(val => val.UserID === this.radioDef);
this.RadioItem["OrgName"] = radioitem.UserName;
this.RadioItem["ID"] = this.radioDef;
this.radioname = radioitem.UserName;
return this.RadioItem;
}
}
},
watch: {
show(bool) {
this.ovoIndex = 0;
if (bool) {

if (this.defaultUser.length === 0) {
this.userName = [];
this.checkList = [];
}
try {
plus.key.removeEventListener("backbutton", () => {});
plus.key.addEventListener("backbutton", () => {
this.$emit("close");
});
} catch (err) {}
}
if (!bool) {
try {
plus.key.removeEventListener("backbutton", () => {});
plus.key.addEventListener("backbutton", () => {
this.$router.back();
});
} catch (err) {}
}
},
disabCheck(dis) {
this.discheck = dis;
},
defaultUser() {
this.init();
}
},
mounted() {
this.getAllUser();
this.init();
},
methods: {
getAllUser() {
axios
.get(
`${api45}/api/v1.0/OrgManage/GetOrgUserList?orgId=0&page=1&pageSize=3000&v=0.8117229161569102`
)
.then(res => {
this.allUser = res.data.Data.UserList;
});
},
init() {
let sbar = this.defaultUser.map(val => {
return {
ID: val.UserID,
OrgName: val.Name
};
});
this.userName = sbar.map(val => val.OrgName);
let lo = {};
this.checkList = this.checkList.concat(sbar).reduce((item, next) => {
lo[next["ID"]] ? "" : (lo[next["ID"]] = true && item.push(next));
return item;
}, []);
},
tabLeft() {
this.$emit("close");
if (this.defaultUser) {
this.checkList = this.defaultUser.map(val => {
return {
ID: val.UserID,
OrgName: val.Name
};
});
this.userName = this.defaultUser.map(val => val.Name);
} else {
this.userName = [];
this.checkList = [];
}
},
selectUserType(index) {
this.ovoIndex = index;
},
deleteUser(index) {
this.userName.splice(index, 1);
this.checkList.splice(index, 1);
},
subm() {
let ar = [];
if (sessionStorage.selectuser) {
ar = JSON.parse(sessionStorage.selectuser);
}
if (!this.radioBool) {
this.$emit("save", this.checkList);
if (!this.checkList.length) return;
let t = {};
let osar = ar.concat(this.checkList).reduce((item, next) => {
t[next["ID"]] ? "" : (t[next["ID"]] = true && item.push(next));
return item;
}, []);
sessionStorage.setItem("selectuser", JSON.stringify(osar));
} else {
this.$emit("save", this.RadioItem);
if (!this.RadioItem["ID"]) return;
let t = {};
let osar = [this.RadioItem, ...ar].reduce((item, next) => {
t[next["ID"]] ? "" : (t[next["ID"]] = true && item.push(next));
return item;
}, []);
sessionStorage.setItem("selectuser", JSON.stringify(osar));
}
},
chList(list) {
let ar = [];
this.allUser.forEach(val => {
list.forEach(id => {
if (val.UserID === id) {
ar.push(val);
}
});
});
this.userName = ar.map(item => item.UserName);
this.checkList = [];
ar.forEach((val, index) => {
this.checkList[index] = {};
this.checkList[index]["ID"] = val.UserID;
this.checkList[index]["OrgName"] = val.UserName;
});
},
radChange(item) {
this.RadioItem["ID"] = item;
let Item = this.allUser.find(val => val.UserID === item);
this.RadioItem["OrgName"] = Item.UserName;
this.radioname = Item.UserName;
}
}
};



