import scroll from "../../scrollAll";
import notData from "../../noData";
export default {
  data() {
    return {
      treeList: [],
      tabTxtArr: [],
      cheboxList: [],
      radio: 0
    };
  },
  components: {
    scroll,
    notData
  },
  props: {
    radioBool: {
      type: Boolean
    },
    defaultUser: {
      type: Array
    },
    ovoIndex: {
      type: [Number, String]
    },
    disabCheck: {
      type: [Array]
    },
    radioDef: {
      type: [Object, Number]
    }
  },
  watch: {
    ovoIndex() {
      if (this.defaultUser) {
        this.cheboxList = this.defaultUser.map(val => val.ID);
      }
      if (this.radioDef) {
        this.radio = this.radioDef.ID || this.radioDef;
      }
      this.$refs["scroll"].refresh();
    },
    defaultUser() {
      if (this.cheboxList) {
        this.cheboxList = this.defaultUser.map(val => val.ID);
      }
    }
  },
  methods: {
    getTree(orgid) {
      this.$vux.loading.show({
        text: ""
      });
      this.Axios.get(
        `${api50}/api/v1/Org/GetOrgUserList?authType=${3}&orgId=${orgid}`
      )
        .then(res => {
          this.treeList = res.data.Data;
          this.$vux.loading.hide();
        })
        .catch(err => {
          console.log(err);
        });
    },
    toUser(item) {
      // this.$emit("todeli", item);
      this.getTree(item.ID);
      this.tabTxtArr.push(item);
    },
    toTab(item) {
      this.getTree(item.ID);
      let index = this.tabTxtArr.indexOf(item) + 1;
      let last = this.tabTxtArr.length;
      this.tabTxtArr.splice(index, last);
    },
    closeTab() {
      this.tabTxtArr = [];
      this.getTree(0);
    },
    changeChebox() {
      this.$emit("checkList", this.cheboxList);
    },
    checkValue(item) {
      let arr = this.cheboxList.find(val => val === item.ID);
      if (arr) {
        this.cheboxList.splice(this.cheboxList.indexOf(arr), 1);
      } else {
        // let len = this.disabCheck.find(val => val === item.ID);
        // if (len) {
        //   return false;
        // }
        this.cheboxList.push(item.ID);
      }
      this.$emit("checkList", this.cheboxList);
    },
    radioChange() {
      this.$emit("radioChange", this.radio);
    },
    checkradioValue(item) {
      this.radio = item.ID;
      this.$emit("radioChange", this.radio);
    }
  },
  mounted() {
    this.getTree(0);
    let hei = document.body.clientHeight;
    setTimeout(() => {
      this.$refs.clientList.style.height = `${hei - 160}px`;
    }, 0);
  }
};



