import scroll from "../../scrollAll";
import nodata from "../../noData";
export default {
  data() {
    return {
      sessDefuser: [],
      radio: null,
      list: []
    };
  },
  components: {
    scroll,
    nodata
  },
  props: {
    defaultUser: {
      type: Array
    },
    radioBool: {
      type: Boolean
    },
    ovoIndex: {
      type: [Number, String]
    },
    disabCheck: {
      type: [Array]
    },
    radioDef: {
      type: [Object, Number]
    }
  },
  watch: {
    ovoIndex() {
      if (this.defaultUser) {
        this.sessDefuser = this.defaultUser.map(val => val.ID);
      }
      if (this.radioDef) {
        this.radio = this.radioDef.ID || this.radioDef;
      }
    },
    defaultUser() {
      if (this.sessDefuser) {
        this.sessDefuser = this.defaultUser.map(val => val.ID);
      }
    }
  },
  computed: {
    lists() {
      if (sessionStorage.selectuser) {
        return JSON.parse(sessionStorage.selectuser);
      } else {
        return [];
      }
    }
  },
  methods: {
    checkList() {
      this.$emit("checkList", this.sessDefuser);
    },
    init() {
      if (this.defaultUser) {
        this.sessDefuser = this.defaultUser.map(val => val.ID);
      }
      if (this.radioDef) {
        this.radio = this.radioDef.ID || this.radioDef;
      }
    },
    checkValue(item) {
      let arr = this.sessDefuser.find(val => val === item.ID);
      if (arr) {
        this.sessDefuser.splice(this.sessDefuser.indexOf(arr), 1);
      } else {
        // let len = this.disabCheck.find(val => val === item.ID);
        // if (len) {
        //   return false;
        // }
        this.sessDefuser.push(item.ID);
      }
      this.$emit("checkList", this.sessDefuser);
    },
    radioChange() {
      this.$emit("radioChange", this.radio);
    },
    checkradioValue(item) {
      this.radio = item.ID;
      this.$emit("radioChange", this.radio);
    }
  },
  mounted() {
    let hei = document.body.clientHeight;
    this.init();
    setTimeout(() => {
      this.$refs.clientList.style.height = `${hei - 160}px`;
      this.$refs.scroll.refresh();
    }, 0);
  }
};



