
import BScroll from "better-scroll";
import Bubble from "./bubble.vue";
import LoadMore from 'vux/src/components/load-more/index.vue'

import { getRect } from "../common/js/dom";

const COMPONENT_NAME = "scroll";
const DIRECTION_H = "horizontal";
const DIRECTION_V = "vertical";

export default {
name: COMPONENT_NAME,
props: {
probeType: {
type: Number,
default: 1
},
height: {
type: Number,
default: 300
},
click: {
type: Boolean,
default: true
},
listenScroll: {
type: Boolean,
default: false
},
listenBeforeScroll: {
type: Boolean,
default: false
},
direction: {
type: String,
default: DIRECTION_V
},
scrollbar: {
type: null,
default: false
},
pullDownRefresh: {
type: null,
default: false
},
pullUpLoad: {
type: null,
default: false
},
startY: {
type: Number,
default: 0
},
refreshDelay: {
type: Number,
default: 20
},
freeScroll: {
type: Boolean,
default: false
},
mouseWheel: {
type: Boolean,
default: false
},
bounce: {
default: true
}
},
data() {
return {
beforePullDown: true,
isRebounding: false,
isPullingDown: false,
isPullUpLoad: false,
pullUpDirty: true,
pullDownStyle: "",
bubbleY: 0
};
},
computed: {
pullUpTxt() {
const moreTxt =
(this.pullUpLoad &&
this.pullUpLoad.txt &&
this.pullUpLoad.txt.more) ||
"";

const noMoreTxt =
(this.pullUpLoad &&
this.pullUpLoad.txt &&
this.pullUpLoad.txt.noMore) ||
"";

return this.pullUpDirty ? moreTxt : noMoreTxt;
},
refreshTxt() {
return (this.pullDownRefresh && this.pullDownRefresh.txt) || "";
}
},
created() {
this.pullDownInitTop = -50;
},
mounted() {
setTimeout(() => {
this.initScroll();
},20);
},
methods: {
initScroll() {
if (!this.$refs.wrapper) {
return;
}
if (
this.$refs.listWrapper &&
(this.pullDownRefresh || this.pullUpLoad)
) {
this.$refs.listWrapper.style.minHeight = this.height + 1 + 'px';
}

let options = {
probeType: this.probeType,
click: this.click,
scrollY: this.freeScroll || this.direction === DIRECTION_V,
scrollX: this.freeScroll || this.direction === DIRECTION_H,
scrollbar: this.scrollbar,
pullDownRefresh: this.pullDownRefresh,
pullUpLoad: this.pullUpLoad,
startY: this.startY,
freeScroll: this.freeScroll,
mouseWheel: this.mouseWheel,
bounce: this.bounce
};

this.scroll = new BScroll(this.$refs.wrapper,options);

if (this.listenScroll) {
this.scroll.on("scroll",pos => {
this.$emit("scroll",pos);
});
}

if (this.listenBeforeScroll) {
this.scroll.on("beforeScrollStart",() => {
this.$emit("beforeScrollStart");
});
}

if (this.pullDownRefresh) {
this._initPullDownRefresh();
}

if (this.pullUpLoad) {
this._initPullUpLoad();
}
},
disable() {
this.scroll && this.scroll.disable();
},
enable() {
this.scroll && this.scroll.enable();
},
refresh() {
this.scroll && this.scroll.refresh();
},
scrollTo() {
this.scroll && this.scroll.scrollTo.apply(this.scroll,arguments);
},
scrollToElement() {
this.scroll &&
this.scroll.scrollToElement.apply(this.scroll,arguments);
},
clickItem(e,item) {
this.$emit("click",item);
},
destroy() {
this.scroll.destroy();
},
forceUpdate(dirty) {
if (this.pullDownRefresh && this.isPullingDown) {
this.isPullingDown = false;
this._reboundPullDown().then(() => {
this._afterPullDown();
});
} else if (this.pullUpLoad && this.isPullUpLoad) {
this.isPullUpLoad = false;
this.scroll.finishPullUp();
this.pullUpDirty = dirty;
this.refresh();
} else {
this.refresh();
}
},
_initPullDownRefresh() {
this.scroll.on("pullingDown",() => {
this.beforePullDown = false;
this.isPullingDown = true;
this.$emit("pullingDown");
});

this.scroll.on("scroll",pos => {
if (!this.pullDownRefresh) {
return;
}
if (this.beforePullDown) {
this.bubbleY = Math.max(0,pos.y + this.pullDownInitTop);
this.pullDownStyle = `top:${Math.min(
pos.y + this.pullDownInitTop,
10
)}px`;
} else {
this.bubbleY = 0;
}

if (this.isRebounding) {
this.pullDownStyle = `top:${10 -
(this.pullDownRefresh.stop - pos.y)}px`;
}
});
},
_initPullUpLoad() {
this.scroll.on("pullingUp",() => {
this.isPullUpLoad = true;
this.$emit("pullingUp");
});
},
_reboundPullDown() {
const { stopTime = 600 } = this.pullDownRefresh;
return new Promise(resolve => {
setTimeout(() => {
this.isRebounding = true;
this.scroll.finishPullDown();
resolve();
},stopTime);
});
},
_afterPullDown() {
setTimeout(() => {
this.pullDownStyle = `top:${this.pullDownInitTop}px`;
this.beforePullDown = true;
this.isRebounding = false;
this.refresh();
},this.scroll.options.bounceTime);
}
},
components: {
LoadMore,
Bubble
}
};



