
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import { mapState, mapGetters } from "vuex";
export default {
name: "ClientLink",
props: {
data: {
type: Object,
default: () => ({
CustomerID: 0,
CustomerName: "",
LinkUserID: 0
})
},
isSelect: {
type: Boolean,
default: true
},
width: {
default: 85
},
path: {
default: "/addFollow/selectCust"
},
workRu: {
type: Boolean,
default: true
},
reqLink: {
type: Boolean,
default: true
}
},
data() {
return {
CustomerName: "",
CustomerID: 0,
cLinkman: [],
LinkUser: [],
LinkUserID: 0,
LinkUserName: "",
isUserShow: false,
styles: {}
};
},
mounted() {
let load = true;
this.$bus.on("selectedClient", data => {
this.CustomerID = data.ID;
this.CustomerName = data.CustomerName;
this.LinkUserID = data.LinkID;
load = false;
this.$emit("selectedClient", data);

});
if (load) {
this.CustomerID = this.data.CustomerID;
this.CustomerName = this.data.CustomerName;
this.LinkUserID = this.data.LinkUserID;
}


},
methods: {
selectCust() {
if (!this.isSelect) return;
this.$router.push({
path: this.path
});
},
beforeShow() {
if (!this.CustomerID) {
this.$vux.toast.show({
type: "warn",
text: ""
});
this.isUserShow = false;
return false;
} else {
this.isUserShow = true;
}
},

getLinkman() {
return new Promise(resolve => {
this.getAxios(`/Customer/GetPagedCustomerLinkManList`, {
page: 1,
count: 100,
customerId: this.CustomerID
}).then(res => {
this.cLinkman = [];
let list = [];
res.Data.List.forEach(item => {
list.push({
value: `${item.ID}`,
name: item.LinkName
});
});
this.cLinkman.push(list);
this.LinkUser = [`${this.LinkUserID}`];
resolve();
});
});
},
getData() {
return {
LinkUserID: this.LinkUserID,
CustomerName: this.CustomerName,
CustomerID: this.CustomerID
};
}
},
watch: {
CustomerID() {
if (this.CustomerID) {
this.getLinkman();
}
},
"data.CustomerID"() {

this.CustomerID = this.data.CustomerID;
this.CustomerName = this.data.CustomerName;
this.LinkUserID = this.data.LinkUserID;

}
},
components: {
XInput,
PopupPicker
}
};



