
import PopupRadio from 'vux/src/components/popup-radio/index.vue'

import { mapActions, mapState, mapGetters } from "vuex";

export default {
name: "Dict",
props: {
title: {
default: ""
},
required: {
default: true
},
placeholder: {
default: ""
},
type: {
default: 4
},
value: {
default: 0
},
ifdefault: {
default: true
}
},
data() {
return {
options: [],
dictData: [],
dictID: 0,
dictName: ''
};
},
mounted() {
this.initDict();
this.dictID = this.value;
},
methods: {
...mapActions(["initDictPromise"]),
initDict() {
return new Promise(resolve => {
this.initDictPromise(this.type).then(() => {

let list = [];
this.dict[this.type].forEach(item => {
list.push({
value: item.DictName,
key: item.ID
});
});
this.dictData = list;
if(this.ifdefault){
this.dictID = this.dictID || list[0].key
}
this.$emit('input',this.dictID)
resolve();
});
});
},
selected (dictID) {
this.$emit('input',dictID)
if(!this.ifdefault){
this.dictData.forEach(item=>{
if(item.key == dictID){
this.$emit('cType',item.value)
}
})
}
}
},
watch: {
value() {
this.dictID = this.value;
},
},
computed: {
...mapState(["dict"])

},
components: {
PopupRadio
}
};



