
import scroll from "../../scrollAll";
import notData from "../../noData";
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
data() {
return {
allUser: [],
cheboxList: [],
radio: 0,
searchValue: "",
keyWord: "",
timer: null
};
},
components: {
scroll,
notData,
XInput,
Group
},
props: {
radioBool: {
type: Boolean
},
defaultUser: {
type: Array
},
ovoIndex: {
type: [Number, String]
},
disabCheck: {
type: [Array]
},
radioDef: {
type: [Object, Number]
}
},
mounted() {
this.getAllUser();
let hei = document.body.clientHeight;
setTimeout(() => {
this.$refs.clientList.style.height = `${hei - 160}px`;
}, 0);
},
watch: {
ovoIndex() {
if (this.defaultUser) {
this.cheboxList = this.defaultUser.map(val => val.ID);
}
if (this.radioDef) {
this.radio = this.radioDef.ID || this.radioDef;
}
this.$refs["scroll"].refresh();
},
defaultUser() {
if (this.cheboxList) {
this.cheboxList = this.defaultUser.map(val => val.ID);
}
}
},
methods: {
getAllUser() {
this.$vux.loading.show({
text: ""
});
axios
.get(
`${api45}/api/v1.0/OrgManage/GetOrgUserList?orgId=0&page=1&pageSize=3000&v=0.8117229161569102&keyword=${
this.keyWord
}`
)
.then(res => {
this.allUser = res.data.Data.UserList;
this.$refs["scroll"].refresh();
this.$vux.loading.hide();
});
},
checkValue(item) {
let arr = this.cheboxList.find(val => val === item.UserID);
if (arr) {
this.cheboxList.splice(this.cheboxList.indexOf(arr), 1);
} else {
this.cheboxList.push(item.UserID);
}
this.$emit("checkList", this.cheboxList);
},
changeChebox() {
this.$emit("checkList", this.cheboxList);
},
checkradioValue(item) {
this.radio = item.UserID;
this.$emit("radioChange", this.radio);
},
radioChange() {
this.$emit("radioChange", this.radio);
},
searchVChange(v) {
this.keyWord = v;
if (this.timer) {
clearTimeout(this.timer);
}
this.timer = setTimeout(() => {
this.getAllUser();
}, 16);

}
}
};



